import React, { FC } from 'react';
import { Link } from 'gatsby';
import { css } from '@emotion/core';

interface IProps {
  title: string;
}

const Header: FC<IProps> = ({ title }) => (
  <header css={headerStyle}>
    <div css={headerTopStyle}>
      <h1 css={h1Style}>
        <Link to="/">{title}</Link>
      </h1>
      <h1 css={h1Style}>
        <Link to="/">{title}</Link>
      </h1>
      <h1 css={h1Style}>
        <Link to="/">{title}</Link>
      </h1>
    </div>
  </header>
);

const headerStyle = css`
  position: fixed;
  display: flex;
  flex-direction: column;
  justify-content: center;
  left: 0;
  top: 0;
  width: 100%;
  min-height: 50px;
  color: #fff;
  background: transparent;
  padding: 0 25px;
`;

const headerTopStyle = css`
  display: flex;
  align-items: center;
  justify-content: space-between;
  min-height: 50px;
  width: 100%;
`;

const h1Style = css`
  color: #fff;
  margin: 0;

  & > a {
    color: #fff;
  }
`;

export default Header;
